import React from "react";
import PropTypes from "prop-types";
import serviceBackground from "../../assets/images/work_animations_patch.svg";

export const Service = ({
  className = "service",
  background = serviceBackground,
  gif = "",
  title = "",
}) => (
  <div className={className}>
    <img src={background} alt="" className="service-background" />
    <img src={gif} className="service-gif" />
    <div className="service-title">
      <div>{title}</div>
    </div>
  </div>
);

Service.propTypes = {
  className: PropTypes.string,
  background: PropTypes.string,
  gif: PropTypes.any,
  title: PropTypes.string,
};
