import React from "react";
import { useTranslation } from "react-i18next";
import FadeInSection from "../../../util/FadeInSection";
import { useNavigate } from "react-router-dom";
import backRight from "../../../assets/images/back-roles-right.svg";
import backLeft from "../../../assets/images/back-roles-left.svg";

const OpenRoles = () => {
  const { t } = useTranslation("common");
  const navigate = useNavigate();
  const benefits = [
    {
      title: "openRoles.front",
      route: "front-end",
      modality: "openRoles.remote",
    },
    {
      title: "openRoles.fullStack",
      route: "full-stack",
      modality: "openRoles.remote",
    },
    {
      title: "openRoles.back",
      route: "back-end",
      modality: "openRoles.remote",
    },
  ];
  return (
    <>
      <section className="openRoles" id="roles">
        <img className="back-right" src={backRight} />
        <img className="back-left" src={backLeft} />
        <div className="container-openRoles">
          <div className="box">
            <FadeInSection>
              <h1>{t("openRoles.title")}</h1>
              <p className="info-roles">{t("openRoles.text")}</p>
            </FadeInSection>
            {benefits.map((elem, index) => (
              <FadeInSection key={index}>
                <button
                  className="btn btn-roles"
                  onClick={() => {
                    navigate(`/details/${elem.route}`);
                  }}
                >
                  <h2 className="btn-title">{t(elem.title)}</h2>
                  <p className="btn-modality">{t(elem.modality)}</p>
                </button>
              </FadeInSection>
            ))}
          </div>
        </div>
      </section>
    </>
  );
};

export default OpenRoles;
