import React from "react";
import { Routes, Route, BrowserRouter as Router } from "react-router-dom";
import LandingPage from "../view/LandingPage";
import HiringPage from "../view/HiringPage";
import DetailedJob from "../view/WeHiring/DetailedJob/DetailedJob";

const AppRoutes = () => {
  return (
    <Router>
      <Routes>
        <Route path="/" element={<LandingPage />} />
        <Route path="/we-hiring" element={<HiringPage />} />
        <Route path="/details/:id" element={<DetailedJob />} />
      </Routes>
    </Router>
  );
};

export default AppRoutes;
