/* eslint-disable react/prop-types */
import React, { useEffect, useState } from "react";
import n_blue from "../assets/n_blue.svg";
import { useTranslation } from "react-i18next";
import logo from "../assets/logo.svg";
import flag_es from "../assets/flag_es.svg";
import flag_en from "../assets/flag_en.svg";
import menu_blue from "../assets/menu_blue.svg";
import close_blue from "../assets/close_blue.svg";
import Menu from "./Menu";
import Scrollspy from "react-scrollspy";
import PropTypes from "prop-types";
import { useNavigate } from "react-router-dom";

const NavBar = ({ type = "landing" }) => {
  const { i18n, t } = useTranslation("common");
  const [openSelectLanguage, setOpenSelectLanguage] = useState(false);
  const [isScroll, setisScroll] = useState(false);
  const [isOpenMenu, setisOpenMenu] = useState(false);
  const navigate = useNavigate();
  const [language, setLanguage] = useState(
    localStorage.getItem("languaje") ? localStorage.getItem("languaje") : "es"
  );
  const changeLanguage = (lng) => {
    i18n.changeLanguage(lng === "es" ? "es" : lng);
    setOpenSelectLanguage(false);
    localStorage.setItem("languaje", lng);
  };
  const scrolled = () => {
    if (window.scrollY >= 0.1 * window.innerHeight) {
      setisScroll(true);
    } else {
      setisScroll(false);
    }
  };
  const closeMenu = () => {
    if (window.innerWidth >= 980) {
      setisOpenMenu(false);
    }
  };
  useEffect(() => {
    window.addEventListener("scroll", scrolled);
    return () => {
      window.removeEventListener("scroll", scrolled);
    };
  });
  useEffect(() => {
    window.addEventListener("resize", closeMenu);
    return () => {
      window.removeEventListener("resize", closeMenu);
    };
  });

  const landingNav = () => {
    return (
      <>
        <img src={logo} alt="" className={isScroll ? "none" : "img-logo1"} />
        <div className={isScroll ? "container container-scroll" : "container"}>
          <img
            src={n_blue}
            alt=""
            className={isScroll ? "none" : "img-n_blue"}
          />
          <a href="#home">
            <img src={logo} alt="" className={isScroll ? "img-logo" : "none"} />
          </a>
        </div>

        <Scrollspy
          items={["about-us-1", "ourServices", "clients"]}
          currentClassName="is-current"
          offset={isScroll ? -80 : 0}
        >
          <li>
            <a href="#about-us-1">{t("navBar.aboutUs")}</a>
          </li>
          <li>
            <a href="#ourServices">{t("navBar.services")}</a>
          </li>
          <li>
            <a href="#clients">{t("navBar.work")}</a>
          </li>

          <button
            className="btn contact"
            onClick={() => navigate("/we-hiring")}
          >
            {t("navBar.contactUs")}
          </button>
          <div className="select-language">
            <div
              className="selected-language"
              onClick={() => setOpenSelectLanguage(!openSelectLanguage)}
            >
              {language == "es" ? (
                <img src={flag_es} alt="" className="flag" />
              ) : (
                <img src={flag_en} alt="" className="flag flag-us" />
              )}
            </div>
            {openSelectLanguage && (
              <div className="language-items-container">
                <div
                  className="language-item"
                  onClick={() => {
                    changeLanguage("en");
                    setLanguage("en");
                  }}
                >
                  <img src={flag_en} alt="" className="flag flag-us" />
                </div>
                <div
                  className="language-item"
                  onClick={() => {
                    changeLanguage("es");
                    setLanguage("es");
                  }}
                >
                  <img src={flag_es} alt="" className="flag" />
                </div>
              </div>
            )}
          </div>
        </Scrollspy>
        <div
          className="menu-icon"
          onClick={() => {
            setisOpenMenu(!isOpenMenu);
          }}
        >
          {isOpenMenu ? (
            <img src={close_blue} alt="" className="img-menu" />
          ) : (
            <img src={menu_blue} alt="" className="img-menu" />
          )}
        </div>
      </>
    );
  };

  const hiringNav = () => {
    return (
      <>
        <img src={logo} alt="" className={isScroll ? "none" : "img-logo1"} />
        <div className={isScroll ? "container container-scroll" : "container"}>
          <img
            src={n_blue}
            alt=""
            className={isScroll ? "none" : "img-n_blue"}
          />
          <a href="#home">
            <img src={logo} alt="" className={isScroll ? "img-logo" : "none"} />
          </a>
        </div>
        <Scrollspy
          items={["about-us-1", "how-is-our-team", "benefits"]}
          currentClassName="is-current"
          offset={isScroll ? -80 : 0}
        >
          <li>
            <a href="#about-us-1">{t("navBar.aboutUs")}</a>
          </li>
          <li>
            <a href="#how-is-our-team">{t("navBar.ourTeam")}</a>
          </li>
          <li>
            <a href="#benefits">{t("navBar.ourBenefits")}</a>
          </li>

          <button className="btn contact" onClick={() => navigate("/")}>
            {t("navBar.home")}
          </button>
          <div className="select-language">
            <div
              className="selected-language"
              onClick={() => setOpenSelectLanguage(!openSelectLanguage)}
            >
              {language == "es" ? (
                <img src={flag_es} alt="" className="flag" />
              ) : (
                <img src={flag_en} alt="" className="flag flag-us" />
              )}
            </div>
            {openSelectLanguage && (
              <div className="language-items-container">
                <div
                  className="language-item"
                  onClick={() => {
                    changeLanguage("en");
                    setLanguage("en");
                  }}
                >
                  <img src={flag_en} alt="" className="flag flag-us" />
                </div>
                <div
                  className="language-item"
                  onClick={() => {
                    changeLanguage("es");
                    setLanguage("es");
                  }}
                >
                  <img src={flag_es} alt="" className="flag" />
                </div>
              </div>
            )}
          </div>
        </Scrollspy>
        <div
          className="menu-icon"
          onClick={() => {
            setisOpenMenu(!isOpenMenu);
          }}
        >
          {isOpenMenu ? (
            <img src={close_blue} alt="" className="img-menu" />
          ) : (
            <img src={menu_blue} alt="" className="img-menu" />
          )}
        </div>
      </>
    );
  };
  const detailsNav = () => {
    return (
      <>
        <div className="container container-scroll">
          <img
            src={logo}
            alt=""
            className="img-logo"
            onClick={() => navigate("/")}
          />
        </div>
        <ul className="ul-details">
          <button
            className="btn contact"
            onClick={(e) => {
              window.location.href = "mailto:info@novateva.com";
              e.preventDefault();
            }}
          >
            {t("front.frontButton")}
          </button>
          <div className="select-language">
            <div
              className="selected-language"
              onClick={() => setOpenSelectLanguage(!openSelectLanguage)}
            >
              {language == "es" ? (
                <img src={flag_es} alt="" className="flag" />
              ) : (
                <img src={flag_en} alt="" className="flag flag-us" />
              )}
            </div>
            {openSelectLanguage && (
              <div className="language-items-container">
                <div
                  className="language-item"
                  onClick={() => {
                    changeLanguage("en");
                    setLanguage("en");
                  }}
                >
                  <img src={flag_en} alt="" className="flag flag-us" />
                </div>
                <div
                  className="language-item"
                  onClick={() => {
                    changeLanguage("es");
                    setLanguage("es");
                  }}
                >
                  <img src={flag_es} alt="" className="flag" />
                </div>
              </div>
            )}
          </div>
        </ul>
      </>
    );
  };

  const selectedNav = () => {
    if (type == "landing") {
      return landingNav();
    } else if (type == "hiring") {
      return hiringNav();
    } else {
      return detailsNav();
    }
  };

  return (
    <header>
      <nav className={isScroll ? "nav nav-scroll" : "nav"}>{selectedNav()}</nav>
      {isOpenMenu && <Menu setisOpenMenu={() => setisOpenMenu(false)} />}
    </header>
  );
};
export default NavBar;

NavBar.PropTypes = {
  type: PropTypes.string,
};
